import {
	makePhoneCall,
	gxgConfirm
} from '@/config/package.js'
import {
	WRITE_ORDER,
	ADDRESS_INDEX,
	REFUND_DETAIL,
	ORDER_LIST,
	HOME_INDEX
} from '@/config/router.js';
import {
	formatOrderButtonState
} from '@/config/filter.js';
import types from '@/store/mutationTypes.js';
import {
	mapMutations,
	mapActions
} from 'vuex';
import {
	deleteOrder,
	getOrderDetail,
	orderDelivery
} from '@/api/orderStatus'

export default {
	props: {
		btnList: {
			type: Array,
			default: () => {
				return []
			}
		},
		orderid: {
			type: Number,
			default: 0
		},
		item: {
			type: Object,
			default: () => {
				return {}
			}
		}
	},
	data() {
		return {
			isShowModel: false,
		}
	},
	computed: {
		canUseBtn() {
			return this.btnList.map(item => formatOrderButtonState(item)).filter(item => item != undefined);
		}
	},
	methods: {
		...mapActions('order', ['getOrderItem','getOrderStatus']),
		...mapMutations('order', [types.SET_ORDER_ITEM]),
		showRefundModel: function() {
			this.isShowModel = !this.isShowModel
		},
		clickBtnEvent: function(e) {
			const id = e.target.dataset.id
			if (id == 1) {
				makePhoneCall({
					phone: '0574-83017657'
				})
			} else if (id == 2) {
				gxgConfirm({
					successFunction: () => {
						deleteOrder(this.orderid).then(res => {
							this.$emit('deleteSuccess',{id:this.orderid});
						})
					}
				})
			} else if (id == 3) {
				uni.navigateTo({
					url: `${WRITE_ORDER}?id=${this.orderid}`
				})
			} else if (id == 4) {
				uni.navigateTo({
					url: `${ADDRESS_INDEX}?srouce=orderBtn&orderid=${this.orderid}`
				})
			} else if (id == 5) {
				getOrderDetail(this.orderid).then(res => {
					gxgConfirm({
						title: '物流单号',
						content: res.deliveryId,
						showCancel: false,
						confirmText: '确认复制',
						successFunction: () => {
							uni.setClipboardData({
								data: res.deliveryId,
								success: function() {
									uni.showToast({
										title: '已复制到剪切板',
										icon: 'none'
									})
								}
							});
						}
					})
				})

			} else if (id == 6) {
				orderDelivery(this.orderid).then(res => {
					uni.showToast({
						title: '收货成功',
						duration: 2000,
						icon: 'none',
						success: () => {
							this.$emit('updateList')
							uni.navigateTo({
								url: `${ORDER_LIST}?state=4`,
								success: ()=>{
									this.getOrderStatus()
								}
							})
						}
					})
				})
			} else if (id == 7) {
				makePhoneCall({
					phone: '0574-83017657'
				})
			} else if (id == 8) {
				uni.navigateTo({
					url: `${WRITE_ORDER}?id=${this.orderid}`
				})
			} else if (id == 9) {
				this.showRefundModel();
				this[types.SET_ORDER_ITEM](this.item);
				uni.navigateTo({
					url: `${REFUND_DETAIL}?id=${this.id}`
				})
			} else if (id == 10) {
				uni.switchTab({
					url: HOME_INDEX
				})
			}
		}
	}
}
